.Main {
    position: relative;
    margin-bottom: 10vh;
    width: 70%;

    order: 0;
    flex: 2 0 auto;
    align-self: auto;
}

.Main .jokes-wrapper {
    max-width: 80%;
    margin: 40px auto;
}


.Main form {
    margin-bottom: 40px;
}

.Main .categories__checked {
    margin-top: 13px;
    margin-bottom: 3px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
}

.Main .categories__checked li.btn-categories {
    order: 0;
    flex: 0 1 auto;
    align-self: auto;

    margin-right: 10px;
    margin-bottom: 10px;
    padding: 6px 15px;
    border: 2px solid #F8F8F8;
    box-sizing: border-box;
    border-radius: 6px;

    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    /* identical to box height, or 133% */

    letter-spacing: 2px;
    text-transform: uppercase;

    color: #ABABAB;
    background: #FFFFFF;
}

.Main .categories__checked li.btn-categories:hover,
.Main .categories__checked li.active {
    color: #333333;
    background: #F8F8F8;
    transition: 300ms ease-in-out 0s;
}

.Main .search__checked input {
    border: 2px solid #333333;
    box-sizing: border-box;
    border-radius: 10px;
    width: 100%;
    height: 42px;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 10px;
}

.Main .search__checked input::-webkit-input-placeholder {
    font-size: 16px;
    line-height: 22px;
    /* identical to box height, or 137% */

    color: #ABABAB;
}

.Main .search__checked input:-ms-input-placeholder {
    font-size: 16px;
    line-height: 22px;
    /* identical to box height, or 137% */

    color: #ABABAB;
}

.Main .search__checked input::-ms-input-placeholder {
    font-size: 16px;
    line-height: 22px;
    /* identical to box height, or 137% */

    color: #ABABAB;
}

.Main .search__checked input::placeholder {
    font-size: 16px;
    line-height: 22px;
    /* identical to box height, or 137% */

    color: #ABABAB;
}

.Main button.btn-submit {
    margin-top: 10px;
    padding: 10px 40px;

    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    background: linear-gradient(92.01deg, #8EA7FF 0%, #7291FF 100%);
    border-radius: 10px;
    border: 0;
}

.Main button.btn-submit:hover, button.btn-submit:focus {
    background: linear-gradient(92.01deg, #6a89fa 0%, #4870fd 100%);
}

.Main button.btn-submit:active {
    background: linear-gradient(92.01deg, #5176fc 0%, #214bde 100%);
}

@media screen and (max-width: 960px) {
    .Main {
        width: 100%;
        position: fixed;
        overflow: scroll;
        max-height: 100%;
    }

    .Main::after {
        display: block;
        content: "";
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.3);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    .Main__disable::after{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
